@import url(https://fonts.googleapis.com/css?family=Arima+Madurai:300);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ant-page-header-ghost {
  background: #bcaf79;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 86px;
}
.ant-select-selector {
  background: #fcf5e7 !important;
}
.arrow-wind svg {
  fill: #ff4d4f;
  font-size: 20px;
  margin-top: -15px;
}
.search-block {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.find-wrapper {
  display: flex;
  justify-content: space-between;
  width: 44%;
}

.ant-select {
  width: 470px !important;
}

.selectCountry {
  width: 200px !important;
}

.ant-page-header-content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.link-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.link-group ul {
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;
}
.link-group ul li {
  padding: 10px;
}

.link-group ul li a svg:hover {
  fill: #ee7009;
  transition: 0.3s;
  transform: scale(1.2);
}

.link-group ul li a svg {
  width: 24px;
  height: 24px;
  fill: #636363;
}

.weather-block {
  height: calc(100vh - 86px);
  width: 100%;
  background-color: #f4ecd0;
  display: flex;
  justify-content: space-evenly;
  /* padding: 30px; */
  flex-wrap: wrap;
}
.wrapper-card {
  width: 350px;
  height: 400px;
  margin: 30px;
  background-color: #fff;
  display: flex;
  justify-content: inherit;
  flex-direction: column;
  align-items: center;
  padding: 40px 0px;
  border-radius: 15px;
  box-shadow: -3px 3px 20px 2px rgba(34, 60, 80, 0.24);
  transition: 0.15s;
}

.wrapper-card:hover {
  width: 350px;
  height: 405px;
}

.control-btns {
  width: 60%;
  display: flex;
  justify-content: space-between;
}
.ant-btn {
  border-radius: 5px;
}
.cityName {
  font-weight: 500;
}
.control-btns .update-btn {
  background-color: rgb(97, 139, 49) !important;
  border: none;
  outline: none;
  color: #fff;
}
.card-list {
  text-align: center;
  font-size: 16px;
}

.popup-block {
  background: #fff;
  width: 375px;
  padding: 10px;
  margin: 0;
  border-radius: 10px;
}
.popup-wrapper {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  position: relative;
}

.popup-block .ant-btn.hide-popup-btn {
  position: absolute !important;
  top: -15px !important;
  right: -15px !important;
  background-color: #fff;
  width: 20px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
}
.ant-btn.hide-popup-btn svg {
  height: 15px;
  width: 14px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
h1 {
  color: black;
  letter-spacing: -3px;
  text-shadow: 0px 1px 1px rgb(255 255 255 / 60%);
  position: relative;
  z-index: 3;
  font-size: 45px;
  font-weight: 400;
}
.container {
  z-index: 1;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 35rem;
  background-image: linear-gradient(
      to bottom,
      rgba(255, 168, 76, 0.6) 0%,
      rgba(255, 123, 13, 0.6) 100%
    ),
    url("https://images.unsplash.com/photo-1446824505046-e43605ffb17f");
  background-blend-mode: soft-light;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.bird {
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/174479/bird-cells-new.svg);
  background-size: auto 100%;
  width: 88px;
  height: 125px;
  will-change: background-position;
  animation-name: fly-cycle;
  animation-timing-function: steps(10);
  animation-iteration-count: infinite;
}
.bird--one {
  animation-duration: 1s;
  animation-delay: -0.5s;
}
.bird--two {
  animation-duration: 0.9s;
  animation-delay: -0.75s;
}
.bird--three {
  animation-duration: 1.25s;
  animation-delay: -0.25s;
}
.bird--four {
  animation-duration: 1.1s;
  animation-delay: -0.5s;
}
.bird-container {
  position: absolute;
  top: 20%;
  left: -10%;
  transform: scale(0) translateX(-10vw);
  will-change: transform;
  animation-name: fly-right-one;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.bird-container--one {
  animation-duration: 15s;
  animation-delay: 0;
}
.bird-container--two {
  animation-duration: 16s;
  animation-delay: 1s;
}
.bird-container--three {
  animation-duration: 14.6s;
  animation-delay: 9.5s;
}
.bird-container--four {
  animation-duration: 16s;
  animation-delay: 10.25s;
}
@keyframes fly-cycle {
  100% {
    background-position: -900px 0;
  }
}
@keyframes fly-right-one {
  0% {
    transform: scale(0.3) translateX(-10vw);
  }
  10% {
    transform: translateY(2vh) translateX(10vw) scale(0.4);
  }
  20% {
    transform: translateY(0vh) translateX(30vw) scale(0.5);
  }
  30% {
    transform: translateY(4vh) translateX(50vw) scale(0.6);
  }
  40% {
    transform: translateY(2vh) translateX(70vw) scale(0.6);
  }
  50% {
    transform: translateY(0vh) translateX(90vw) scale(0.6);
  }
  60% {
    transform: translateY(0vh) translateX(110vw) scale(0.6);
  }
  100% {
    transform: translateY(0vh) translateX(110vw) scale(0.6);
  }
}
@keyframes fly-right-two {
  0% {
    transform: translateY(-2vh) translateX(-10vw) scale(0.5);
  }
  10% {
    transform: translateY(0vh) translateX(10vw) scale(0.4);
  }
  20% {
    transform: translateY(-4vh) translateX(30vw) scale(0.6);
  }
  30% {
    transform: translateY(1vh) translateX(50vw) scale(0.45);
  }
  40% {
    transform: translateY(-2.5vh) translateX(70vw) scale(0.5);
  }
  50% {
    transform: translateY(0vh) translateX(90vw) scale(0.45);
  }
  51% {
    transform: translateY(0vh) translateX(110vw) scale(0.45);
  }
  100% {
    transform: translateY(0vh) translateX(110vw) scale(0.45);
  }
}

